/* flexbox columns */

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: center;
  width: 100%;
}

.customRow {
  margin-left: 20vw;
  width: 60vw;
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1;
}

/* header styles */

.header {
  font-family: "IBM Plex Mono", serif;
  font-size: 1rem;
  font-style: italic;

  color: white;
  background-color: crimson;

  padding-top: 10px;
  padding-bottom: 20px;

  text-align: center;
}

.info {
  font-family: "IBM Plex Sans";
  font-weight: bold;
  font-size: 1.2rem;
  font-style: normal !important;
  position: absolute !important;
  right: 5vmin;
  top: 5vmin;
  cursor: pointer;
}

.title {
  letter-spacing: 0.3rem;
  text-align: center;
}

.monName {
  letter-spacing: 0.3rem;
  text-align: center;
  margin-inline: 10vw;
}

.input {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 1rem;

  padding: 0.8rem;
  margin-bottom: 15px;

  border: none;
  border-radius: 10px;

  width: 250px;

  text-indent: 3px;
}

.input:focus {
  outline: none;
}

.submit {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 1rem;
  font-weight: bold;

  color: white;
  background-color: rgb(100, 8, 8);

  padding: 0.8rem;

  border: none;
  border-radius: 10px;

  width: 100px;

  margin: 10px;

  cursor: pointer;
}

.submit:hover {
  background-color: rgb(63, 0, 0);
}

.navigation {
  font-family: "IBM Plex Mono", sans-serif;
  font-size: 1rem;

  color: white;
  background-color: crimson;

  padding: 0.7rem;

  border: none;

  margin: 0;

  cursor: pointer;
}

.right {
  border-radius: 0 0 0 20px;
}

.left {
  border-radius: 0 0 20px 0;
}

.rightbottom {
  border-radius: 20px 0 0 0;
}

.leftbottom {
  border-radius: 0 20px 0 0;
}

.navigation:hover {
  background-color: rgb(153, 14, 42);
}

/* pokeView */

.pokeview {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 1rem;
}

.nametype-column {
  justify-content: center;
  text-align: center;
  align-items: center;
  margin-bottom: 30px;
}

.monImg {
  width: 300px;
  margin-bottom: 40px;
}

.types {
  font-family: "IBM Plex Sans", sans-serif;

  margin-left: 5px;
  margin-right: 5px;

  padding-block: 10px;
  padding-inline: 15px;

  border-radius: 25px;
}

.stats-column {
  justify-content: center;
  align-items: center;
  padding: 30px;
}

.stats {
  font-family: "IBM Plex Mono", serif;
  font-size: 0.7rem;
}

.stats > h1 {
  display: flex;
}

.abilities {
  margin-inline: 80px;
  justify-content: center;
  text-align: center;
  margin-bottom: 30px;
}

.abilities > h2 {
  font-family: "IBM Plex Mono", sans-serif;
}

.abilityDesc {
  font-family: "IBM Plex Mono";
  text-align: left !important;
}

.evolutions {
  font-family: "IBM Plex Mono";
  font-size: 0.8rem;
  text-align: center;
  align-items: center;
  margin-bottom: 50px;
}

.evolutions > img {
  width: 150px;
}

.pokedexEntry {
  font-family: "IBM Plex Mono";
  font-size: 1.2rem;
  justify-content: center;
  margin-top: 50px;
  margin-bottom: 80px;
  margin-inline: 15vw;
}

.moves {
  font-family: "IBM Plex Mono";
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 80px;
  width: 90vmin;
}

.moves-header {
  font-family: "IBM Plex Sans";
  font-size: 1.2rem;
}

.moves-name {
  text-align: left;
}

.moves-type {
  font-family: "IBM Plex Sans";
  font-weight: 400;
  font-style: normal !important;
  width: 90px;
}

tr > th {
  padding-block: 10px;
}

.funfacts {
  margin-bottom: 80px;
}

.funfacts > h2 {
  font-family: "IBM Plex Mono";
  font-size: 1.1rem;
  text-align: left;
  margin-inline: 15vw;
}

.funfacts-spacer {
  margin-top: 50px;
}

.funfacts-center {
  text-align: center !important;
}

/* footer */

.footer {
  font-family: "IBM Plex Mono", serif;
  font-style: italic;
  font-size: 0.9rem;
  color: lightslategray;
  text-align: center;
  margin-inline: 60px;
}

/* links */

a {
  color: crimson;
  text-decoration: none;
}

a:visited {
  color: crimson;
  text-decoration: none;
}

/* info modal */

body.active-modal {
  overflow-y: hidden;
}

.btn-modal {
  padding: 10px 20px;
  display: block;
  margin: 100px auto 0;
  font-size: 18px;
}

.modal, .overlay {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
}

.overlay {
  background: rgba(0, 0, 0, 0.5);
}

.modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  line-height: 1.4;
  background: #f1f1f1;
  color: black;
  padding: 14px 28px;
  border-radius: 3px;
  width: 70vmin;
}

.modal-content > h2 {
  text-align: left;
  font-family: "IBM Plex Sans";
  font-style: normal;
}

.modal-content > p {
  text-align: left;
  font-style: normal;
}

.close-modal {
  background-color: none;
  border: none;
  font-size: 1.5rem;
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 5px 7px;
  cursor: pointer;
}